<template lang="pug">
    .news-detail-page
        <el-row>
            <el-col :span="24">
                <el-image :src="imgUrl" :fit="fit"></el-image>
            </el-col>
        </el-row>
        <el-row>
          <el-col type="flex" justify="center"></el-col>
        </el-row>
</template>

<script>
import {getNewsDetail} from '../api'
export default {
    name:"NewsDetail",
    data() {
    return {
      fit: "cover",
      imgUrl: require("../assets/img/banner/banner-new.jpg")
    }
  },
  created(){
    console.log(this.$route)
    getNewsDetail({id:this.$route.params.id}).then(res=>{
      console.log(res)
    })
  }
}
</script>

